/* -------------------------------------------------------------- *\
    Global Overwrites
\* -------------------------------------------------------------- */

$global_borderRadius:                   2px !default;

$global-color_black:                    #000;
$global-color_white:                    #FFF;
$global-color_transparent:              transparent;

$global-color_classic:                  #465775;
$global-color_navy:                     #333A56;
$global-color_ash:                      #f0f0f0;
$global-color_powder:                   #01b4bc;

$global-color_primary:                  $global-color_classic !default;
$global-color_primaryAlt:               lighten($global-color_primary, 8%) !default;
$global-color_secondary:                $global-color_navy !default;

$global-color_bg:                       $global-color_white !default;
$global-color_fg:                       lighten(desaturate($global-color_primary, 70%), 8%) !default; // text color | should have high contrast between $global-color_bg

$global-body_bg:                        $global-color_bg !default;

/* -------------------------------------------------------------- *\
    Theme
\* -------------------------------------------------------------- */

$theme-border_color:                    mix($global-color_fg, $global-color_bg, 24%) !default;

$theme-photo_round:                      false !default;
$theme-photo_borderRadius:               $global_borderRadius !default;

$theme-link-hover_textDecoration:        underline !default;

/* -------------------------------------------------------------- *\
    Header
\* -------------------------------------------------------------- */

$header_minHeight:                       50px !default;
$header_bg:                              $global-color_ash !default;
$header_color:                           $global-color_primary !default;
$header-hover_color:                     $global-color_primaryAlt !default;
$header_border:                          1px solid rgba($header_bg, .3) !default;

/* -------------------------------------------------------------- *\
    Panel
\* -------------------------------------------------------------- */

$panel_item-border:                      1px solid #{$theme-border_color} !default;
$panel_item-spacing:                     0 !default;
$panel_item-borderRadius:                0 !default;

/* -------------------------------------------------------------- *\
    Footer
\* -------------------------------------------------------------- */

$footer_bg:                              lighten(desaturate($global-color_secondary, 20%), 8%) !default;
$footer_color:                           $global-color_white !default;
$footer-link_color:                      $global-color_white !default;
$footer-link-hover_color:                $global-color_white !default;

/* -------------------------------------------------------------- *\
    Navigation
\* -------------------------------------------------------------- */

$navigation_bg:                          $global-color_secondary !default;
$navigation_button-bg:                   $global-color_primary !default;

/* -------------------------------------------------------------- *\
    Components
\* -------------------------------------------------------------- */

$component-item_spacing:                 0 !default;

/* -------------------------------------------------------------- *\
    Bullets
\* -------------------------------------------------------------- */

$bullet_borderColor:                     $global-color_powder !default;
$bullet_bg:                              $global-color_powder !default;
