/* -------------------------------------------------------------- *\
    Variables
\* -------------------------------------------------------------- */

$flyout_bg:                             $global-color_white !default;
$flyout-hover_bg:                       darken($global-color_white, 7%) !default;
$flyout_fg:                             mix($global-color_black, $flyout_bg, 70%) !default;

$flyout-link_color:                     $global-color_primary !default;
$flyout-link-hover_color:               $global-color_primaryAlt !default;

$flyout_width:                          200px !default;

$flyout-link-alt_color:                 $global-color_secondary !default;
$flyout-link-altHover_color:            darken($global-color_secondary, 8%) !default;

$flyout_border:                         1px solid $theme-border_color !default;
$flyout_borderRadius:                   $global_borderRadius !default;
$flyout_boxShadow:                      0 5px 10px 0 rgba($global-color_black, 0.15) !default;

$flyout-progress_size:                  24px;

/* -------------------------------------------------------------- *\
    Styles
\* -------------------------------------------------------------- */

.MenuItems,
.FlyoutMenu {
    box-shadow: $flyout_boxShadow;
    position: absolute;
    top: 100% !important;
    min-width: $flyout_width;
    background-color: $flyout_bg;
    border: {
        radius: $flyout_borderRadius;
        style: solid;
        width: 1px;
        color: $global-border_color;
    }
    color: $flyout_fg;

    &::before,
    &::after {
        display: none;
    }

    input[type= "text"],
    textarea,
    ul.token-input-list,
    input.InputBox,
    select {
        border-color: $formElement_borderColor;
        color: $formElement_color;
    }

    .InProgress {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        height: $flyout-progress_size * 2;
        width: 100%;
        padding-top: $flyout-progress_size;
        padding-bottom: $flyout-progress_size;
    
        &::after {
            @include spinnerLoader($flyout-progress_size, 4px);
            content: '';
        }
    }
}

.MenuItems-Reactions {
    top: initial !important;
}

.ReactButton {
    position: relative;
}

.MenuItems.Flags {
    padding: $utility-small_padding;

    .ReactSprite {
        line-height: inherit;
        height: inherit;
    }
}

.FlagMenu.Open .MenuItems {
    left: -10px;
}

.Flyout.Flyout.Flyout,
.ButtonGroup .Dropdown {
    visibility: hidden;
}

.ToggleFlyout.Open .Flyout.Flyout.Flyout,
.ButtonGroup.Open .Dropdown,
.editor-dropdown-open .Flyout.Flyout.Flyout {
    visibility: visible;
}

.MenuItems.MenuItems {
    padding: 0;

    li > a {
        font-size: $global-medium_fontSize;
        line-height: $global-base_lineHeight;
        text-transform: none;
        text-align: initial;
        transition: 0.2s ease-out color;
        padding: $utility-small_padding $utility-xLarge_padding !important;
        min-height: 45px;
        display: flex !important;
        align-items: center;
        margin: 0;

        &:hover,
        &:focus {
            text-decoration: none;
            color: $global-color_fg !important;
            background-color: $global-color_active_primaryBg !important;
        }

        .Alert,
        .Count {
            margin-left: $utility-medium_padding;
        }

        .ReactSprite {
            margin-right: $utility-medium_padding;
        }
    }

    hr {
        margin: 0;
    }

    .PhotoWrap {
        height: 24px;
        width: 24px;
        padding: 0 !important;
        margin: 0 0 0 $utility-small_padding;
    }

    &.MenuItems-Reactions {
        .PhotoWrap {
            position: absolute;
            left: 0;
            margin-left: 12px;
            top: 50%;
            min-height: initial;
            transform: translateY(-50%);
            @include photoWrap(24px);

            + a {
                display: inline-block !important;
                padding-left: 45px !important;
                line-height: 45px;
                padding-right: 12px !important;;
                @include ellipsis;
                overflow: hidden;
                max-width: 100%;
            }
        }

        li {
            position: relative;
            display: flex;
        }
    }
}

.Flyout.Flyout.Flyout,
.ButtonGroup .Dropdown {

    .selectBox-link {
        width: auto;
    }
}

.PopList.Conversations .Item,
.PopList.Activities .Item,
.PopList.Popin .Item {
    margin: $utility-small_padding;
    padding: $utility-small_padding;
    border-radius: $flyout_borderRadius;
    border: none;
    position: relative;
    transition: background 0.2s ease-out;

    a {
        color: $flyout-link_color;
        line-height: inherit;
        text-transform: none;
        padding: 0;

        &:focus,
        &:hover {
            color: $flyout-link-hover_color;
        }
    }

    &:not(.Title):hover {
        background: $flyout-hover_bg;
    }

    &.Empty.Empty {
        text-transform: none;
        margin: $utility-small_padding;

        &:hover {
            background: transparent;
        }
    }

    &.Center,
    &.Title {
        display: block;
        text-align: center;
        color: $flyout_fg;
        font-size: $global-medium_fontSize;
        text-transform: uppercase;
        line-height: $global-base_lineHeight;
        justify-content: center;

        a {
            color: $flyout-link-alt_color;

            &:focus,
            &:hover {
                color: $flyout-link-altHover_color;
            }
        }
    }

    &.Title {
        @include clearfix;

        a {
            position: absolute;
            right: $utility-small_padding;
        }
    }

    &:first-child {
        position: relative;
        overflow: initial;
        margin-bottom: $utility-large_padding;

        &::after {
            content: "";
            position: absolute;
            bottom: -8px;
            left: 0;
            right: 0;
            width: 100%;
            height: 1px;
            background: rgba($global-color_black, 0.2);
        }
    }

    &:last-child {
        position: relative;
        overflow: initial;
        margin-top: 16px;

        &::after {
            content: "";
            position: absolute;
            top: -8px;
            left: 0;
            right: 0;
            width: 100%;
            height: 1px;
            background: rgba($global-color_black, 0.2);
        }
    }

    .Excerpt {
        color: $flyout_fg;
    }

    .Author .PhotoWrap {
        width: $theme-photo-small_size;
        height: $theme-photo-small_size;
    }

    .ItemContent {
        margin-top: -$global-lineHeight_offset;
        margin-left: calc(#{$theme-photo-small_size} + #{$utility-medium_padding});
    }
}

.PopList.Activities .Item.Title {
     a {
        position: absolute;
        right: $utility-small_padding;
        max-width: 30%;
        text-align: right;
        line-height: 1.2em;
    }
}

.PopList.Conversations .Item.Empty + .Item:last-child::after {
    display: none;
}

.PopList.Conversations .HasNew {
    border-radius: $formButton_borderRadius;
    background-color: $bullet_bg;
    border: 1px solid $bullet_borderColor;
    color: $bullet_fg;
    padding: 0px 4px;
}

.PopList .Item.Empty {
    margin-top: $utility-small_padding;
    margin-bottom: $utility-small_padding;

    &::after {
        display: none;
    }
}

.MenuItems a {
    margin: 0 $utility-small_padding;
    border-radius: $component_borderRadius;

    &:hover {
        background: $flyout-hover_bg !important;
    }
}

.Trace {
    clear: both;
}

.MenuItems.ui-autocomplete {
    padding: 0;

    mark {
        background-color: rgba($global-color_warning, .5);
    }

    @include maxWidth {
        width: calc(100% - (#{$utility-large_padding} * 2)) !important;
    }

    a {
        margin: 0;
        font-weight: inherit;

        .Title {
            @include ellipsis;
        }

        &:focus,
        &:hover {
            color: inherit !important;
        }
    }

    mark {
        font-weight: inherit;
    }

    .ui-menu-item {
        padding: $utility-small_padding;

        &:last-child {
            border-bottom: 0;
        }
    }

    .ui-menu-item,
    a .Gloss {
        color: $component-base_color;
        font-size: $component-base_fontSize;
        font-weight: $component-base_fontWeight;
    }

    a:focus .Gloss,
    a:hover .Gloss {
        color: $component-base_color !important;
    }

    .Aside {
        color: $component-meta_color;
        font-size: $component-meta_fontSize;
        font-weight: $component-meta_fontWeight;
    }
}

@include maxWidth($theme-breakpoint_tablet) {

    .ToggleFlyout,
    .ButtonGroup {
        cursor: pointer;

        &::before {
            cursor: pointer;
        }
    }
}

// ---------- Mobile ---------- //

@include maxWidth($theme-breakpoint_smallTable) {

    .ToggleFlyout,
    .ButtonGroup {

        &::before {
            content: "";
            opacity: 0;
            position: fixed;
            bottom: 0;
            left: 0;
            right: 0;
            top: 0;
            height: 100vh;
            width: 100vw;
            padding: 0;
            border: none;
            background: rgba($global-color_black, 0.43);
            z-index: 1;
            pointer-events: none;
        }

        &.Open::before {
            opacity: 1;
            z-index: 1;
            pointer-events: initial;
        }
    }

    .Flyout.Flyout.Flyout,
    .ButtonGroup .Dropdown {
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100vw;
        top: initial !important;
        text-align: center;
        padding: $utility-medium_padding;
        border: none;
        border-top: $component_border;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
        min-height: 85px;
        display: block !important;
        visibility: hidden;
        box-shadow: 0 -2px 10px 2px rgba($global-color_black, 0.1);
        opacity: 0;
        cursor: pointer;
        max-height: 80vh;
        overflow: auto;
        transition:
            visibility 0s linear $global-default_timing,
            transform $global-default_easing $global-default_timing,
            opacity $global-default_easing $global-default_timing;
        z-index: 100;

        &::before {
            display: none;
        }

        .PopList .ItemContent {
            margin-left: 0;
            line-height: 1.3em;
        }

        .PopList {
            .Item {
                text-align: left;
                display: flex;
                padding: $utility-xSmall_padding 0;

                &:last-child {
                    border-bottom: none;
                }
            }
        }

        &.selectBox-content {
            margin-left: auto;
            margin-right: auto;
        }

        .selectBox-link {
            width: auto;
        }

        &::after {
            content: none;
        }

        .Author .PhotoWrap {
            width: $theme-photo_size;
            height: $theme-photo_size;
            padding: 0;
            margin-right: $utility-small_padding;
        }
    }

    .ToggleFlyout.Open .Flyout.Flyout.Flyout,
    .ButtonGroup.Open .Dropdown,
    .editor-dropdown-open .Flyout.Flyout.Flyout {
        visibility: visible;
        transition-delay: 0s;
        opacity: 1;

        &::before {
            opacity: 1;
        }
    }

    .editor-dropdown-open .Flyout.Flyout.Flyout::before {
        pointer-events: none;
    }

    .SpDropdownHandle::before {
        content: $icon-select;
    }

    .Dropdown {
        text-align: center;
    }

    // Editor Specific Flyouts
    .editor {
        .editor-dropdown {
            position: static;
        }

        .Flyout.Flyout.Flyout {
            position: absolute;
            left: 0;
            right: 0;
            border: $flyout_border;
            width: auto;
            min-height: initial;
            top: 32px;
            bottom: initial;

            &::before {
                display: none;
            }
        }
    }
}
